.form{display:flex;align-items:center;justify-content:center;height:100vh;width:100vw;background-image:url(/ufomg.github.io/ufomg/ufomg-fe/static/media/star-bg.c1ffa926.jpg)}.form-inputs{display:flex;flex-direction:column;height:700px;width:400px;justify-content:space-evenly;background-color:#4D769A;align-items:center;border-radius:5px;box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75);-webkit-box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75);-moz-box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75)}label{display:flex;flex-direction:column;align-items:center}.formPhoto{height:700px;width:500px;border-radius:5px;box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75);-webkit-box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75);-moz-box-shadow:0px -1px 27px 3px rgba(186,200,227,0.75)}.form__group{position:relative;padding:15px 0 0;width:50%}.form__field{font-family:inherit;width:100%;border:0;border-bottom:2px solid #000;outline:0;font-size:1.3rem;color:#fff;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__field::-webkit-input-placeholder{color:transparent}.form__field:-ms-input-placeholder{color:transparent}.form__field::placeholder{color:transparent}.form__field:-ms-input-placeholder ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__field:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#000}.form__field:focus{padding-bottom:6px;font-weight:700;border-width:3px;border-image:linear-gradient(to right, #013054, #93eb91);border-image-slice:1}.form__field:focus ~ .form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#013054;font-weight:700}.form__field:required,.form__field:invalid{box-shadow:none}body{font-family:'Poppins', sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:100vh;font-size:1.5rem;background-color:#222222}body .container,body .button,body .submit,body .button span{position:absolute}body .container{position:relative;display:flex;width:170px}body .container a:hover .submit{bottom:0px;transition:all 1.5s ease-in-out}body .container a:hover .button{transition:all 1.5s ease-in-out;box-shadow:0px 0px 38px 13px #1ECE39}body .container .submit{left:55px;bottom:-59px;transition:all 1s ease-in-out}body .container .button{width:171px;height:65px;background:black;border-radius:2px;z-index:3;border:black;transition:all 3s ease-in-out;display:flex;justify-content:center;padding:5px;border-radius:5px}body .container .button span{text-align:center;line-height:70px;margin:0 auto;transition:all 0.3s ease-in-out;color:white}.ufoIcon{width:72px;width:60px}.drop-down{padding:5px 10px;color:#fff;text-align:center;background:#000;height:36px;display:block}

